.oldCounterItem__item {
    flex-basis: 80%;
    margin: 0 5px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.oldCounterItem__item--time {
    flex-basis: auto;
    flex-shrink: 0;
}

.oldCounterItem__item--action {
    text-align: center;
    flex-basis: 5%;
    font-size: 22px;
}

@media (max-width: 550px) {
    .oldCounterItem {
        align-items: flex-start !important;
        flex-direction: column;
    }

    .oldCounterItem__item {
        padding: 5px 10px;
        font-size: 18px;
    }

    .oldCounterItem__item--action {
        font-size: 30px;
    }
}
